<template>
  
</template>
<script>
export default {
  name: 'DelFrame',
  props:{
    delValide: {
      type: Boolean,
      required: true
    },
    delStatus:{
      type:[String, Number],
      default:null
    },
    delMsg:{
      type:String,
      default:null
    }
  },
  watch:{
    'delValide':{
      handler(val){
        // console.log(val);
        if(val){
          this.confirmDeleteRecord();
        }
      },
      deep:true
    },
    'delStatus':{
      handler(val){
        // console.log(val);
        if(val==1){
          this.showDeleteSuccess(this.delMsg);
        }else if(val == 2){
          this.showDeleteDefault(this.delMsg);
        }
      },
      deep:true
    }
  },
  data(){
    return{
      delete_id:''
    }
  },
  methods: {
    confirmDeleteRecord (params) {
      // this.delete_id = params.data.box_skins_id;
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: '',
        text: ``,
        accept: this.deleteRecord,
        cancel: this.cancel,
        acceptText: '',
        cancelText:''
      })
    },
    deleteRecord () {
      let _this = this;
      _this.$emit('closeDel',true);//
      _this.$parent.delValide = false;
    },
    cancel(){
      let _this = this;
      _this.$emit('closeDel',false)//
      _this.$parent.delValide = false;
    },
    showDeleteSuccess (delMsg) {
      this.$vs.notify({
        color: 'success',
        title: '',
        text: delMsg ? delMsg : ''
      })
    },
    showDeleteDefault(delMsg){
      this.$vs.notify({
        color: 'danger',
        title: '',
        text: delMsg ? delMsg :''
      })
    }
  },
  mounted(){
  }
}
</script>
